import axios from 'axios';
import Global from '../../../infrastructure/Common/Global';

class AdminApis {

    adminSettings() {
        return axios.get(Global.adminSettings, Global.httpOptions);
    }

    setAdminSettings(data) {
        return axios.post(Global.adminSettings, data, Global.httpOptions);
    }

    cmsContent() {
        return axios.get(Global.cmsContent, Global.httpOptions);
    }

    savePost(type, data) {

        const headers = { headers: { ...Global.httpOptions.headers, 'Content-type': 'multipart/form-data' } };

        // console.log('headder', headers);

        return axios.post(
            type === 'update' ? Global.updatePost : Global.newPost,
            data,
            headers
        )
    }

    cmsPosts(data) {
        return axios.post(Global.cmsPosts, data, Global.httpOptions);
    }

    postDetail(data) {
        return axios.post(Global.postDetail, data, Global.httpOptions);
    }

    updatePostStatus(data) {
        return axios.post(Global.postStatus, data, Global.httpOptions);
    }

    listCourses() {
        return axios.get(Global.adminCourses, Global.httpOptions);
    }

    getCourse(courseId) {
        return axios.get(Global.adminCourse + '/' + courseId, Global.httpOptions);
    }

    saveCourse(type, data) {

        const headers = { headers: { ...Global.httpOptions.headers, 'Content-type': 'multipart/form-data' } };

        // console.log('headder', headers);

        return axios.post(
            type === 'update' ? Global.updateCourse : Global.newCourse,
            data,
            headers
        )
    }

    changeCourseStatus(data) {
        return axios.post(Global.courseStatus, data, Global.httpOptions);
    }

    getBlogPosts() {
        return axios.get(Global.blogPosts, Global.httpOptions);
    }

    getPartners(){
        return axios.get(Global.allPartners, Global.httpOptions);
    }

    getBanners(){
        return axios.get(Global.allBanners, Global.httpOptions);
    }

}

// export default new AdminApis();
export default new AdminApis();