import React from 'react';
import ReactQuill from "react-quill";

import "react-quill/dist/quill.snow.css";

const NodisEditor = ({ value, onUpdate, name, placeholder, disabled = false, allowImages = false }) => {
    const modules = {
        toolbar: [
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            ["bold", "italic", "underline", "strike", "blockquote"],
            [{ font: [] }],
            [{ align: ["right", "center", "justify"] }],
            [{ list: "ordered" }, { list: "bullet" }, { indent: "-1" }, { indent: "+1" }],
            ...(allowImages ? [["link", "image"]] : [["link"]]), // if "allowImages" is true then [link, image] else [link] only
            [{ color: ["red", "#785412"] }],
            [{ background: ["red", "#785412"] }],
        ],
    };

    const formats = [
        "header",
        "bold",
        "italic",
        "underline",
        "strike",
        "blockquote",
        "list",
        "bullet",
        "indent",
        "link",
        ...(allowImages ? ["image"] : []), // if "allowImages" is true
        "color",
        "background",
        "align",
        "font",
    ];

    const handleChange = (content, delta, source, editor) => {
        onUpdate({ name, value: content });
        // setCode(content);
        //let has_attribues = delta.ops[1].attributes || "";
        //console.log(has_attribues);
        //const cursorPosition = e.quill.getSelection().index;
        // this.quill.insertText(cursorPosition, "★");
        //this.quill.setSelection(cursorPosition + 1);
    };

    return (
        <React.Fragment>
            <ReactQuill
                theme="snow"
                className="nodis-editor"
                modules={modules}
                formats={formats}
                value={value}
                placeholder={placeholder}
                onChange={handleChange}
                readOnly={disabled}
            />
        </React.Fragment>
    );
};

export default NodisEditor;